<template>
  <div class="text-center">
    <v-dialog
      v-model="dialog"
      width="500"
    >
      <template v-slot:activator="{ on, attrs }">
        <div class="lang-btn">
          <v-btn class="indigo" fab small @click="dialog = false" v-bind="attrs" v-on="on">
            <v-icon color="white">mdi-translate</v-icon>
          </v-btn>
        </div>
      </template>

      <v-card>
        <v-card-title>
          {{ $l.system.sections.selection_lang }}
        </v-card-title>

        <v-card-text>
          <v-select
            :items="items"
            :label="$l.system.sections.select_lang"
            outlined
            @change="selectLanguaje"
          >
          </v-select>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            text
            @click="dialog = false"
          >
            {{ $l.system.sections.exit }}
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
export default {
  data: () => ({
    dialog: false,
    items: []
  }),

  mounted () {
    for (const key in this.$allLangs) {
      this.items.push(key)
    }
  },

  methods: {
    selectLanguaje (key) {
      this.$store.dispatch('changeLang', key)
    }
  }
}
</script>

<style lang="sass">
.lang-btn
  position: fixed
  top: 10px
  right: 10px
</style>
